* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-left: 30px;
}

.country-list li {
  display: flex;
  align-items: center;
  margin-left: 30px;

  font-weight: 500;
}

img {
  width: 40px;
  margin-right: 20px;
}

.country-info {
  margin-left: 30px;
}

.country-head {
  display: flex;
  align-items: center;

  font-weight: 700;
}

.accent {
  font-weight: 500;
}